<template>
    <div>
        <!-- 面包屑导航区域 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>排座系统</el-breadcrumb-item>
            <el-breadcrumb-item>在线排座</el-breadcrumb-item>
        </el-breadcrumb>
         <!-- 卡片视图 -->
        <el-card>
            <el-row :gutter="10">
                <el-col :span="3">
                     <div class="schoolSet">学校班级选择：</div>
                </el-col>
                <el-col :span="4">
                     <el-cascader :options="options" v-model="stu_cat" :props="cateProps" @change="handleChange" clearable></el-cascader>
                </el-col>
                 <el-col :span="3">
                     <div class="schoolSet">排座列数选择：</div>
                </el-col>
                <el-col :span="4">
                    <el-select v-model="value" placeholder="请选择排座列数" @focus="handleTypeChange(value)" clearable>
                    <el-option v-for="item in typeoptions" :key="item.value"  :label="item.label"  :value="item.value" >
                    </el-option>
                </el-select>
                </el-col>
                <el-col :span="2">
                     <div class="schoolSet">排座周期：</div>
                </el-col>
                <el-col :span="3">
                    <el-select v-model="time" @change="changeTime"  clearable>
                    <el-option v-for="item in timeoptions" :key="item.value"  :label="item.label"  :value="item.value" >
                    </el-option>
                    </el-select>
                </el-col>
                <el-col :span="3">
                        <el-button type="primary" @click="seatQuery">在线排座</el-button>
                </el-col>
            </el-row>
            <el-row class="row " style=" font-size: 20px;font-weight: bold; margin: 20px 0 10px 0 " v-if="!this.studentList.length">
              <el-col :span="6">方式一:</el-col>
              <el-col :span="6">方式二:</el-col>
              <el-col :span="6">方式三:</el-col>
              <el-col :span="6">方式四:</el-col>
            </el-row>
            <el-row class="row" style="font-size: 20px"  v-if="!this.studentList.length">
              <el-col :span="6">6列排布</el-col>
              <el-col :span="6">7列排布</el-col>
              <el-col :span="6">8列排布</el-col>
              <el-col :span="6">9列排布</el-col>
            </el-row>
            <!-- <el-row style="margin: 20px 0px" v-if="!this.studentList.length">
              <el-col :span="6">
                    <el-image  style="min-height: 200px":src="style1.url" :title="style1.title" fit="fit"></el-image>
              </el-col>
              <el-col :span="6">
                    <el-image  style="min-height: 200px":src="style2.url" :title="style2.title" fit="fit"></el-image>
              </el-col>
             <el-col :span="6">
                   <el-image  style="min-height: 200px":src="style3.url" :title="style3.title" fit="fit"></el-image>
             </el-col>
              <el-col :span="6">
                    <el-image  style="min-height: 200px":src="style4.url" :title="style4.title" fit="fit"></el-image>
              </el-col>
            </el-row> -->
        </el-card>
    </div>
</template>
<script>
import axios from 'axios'
export default {
     created() {
        this.token = window.sessionStorage.getItem('token');
        this.getOPtions();
    },
    data() {
        return {
          showTu: false,
          style1: {url:require('../../assets/image/1.jpg'), title: '方式一', id: 1},
          style2: {url:require('../../assets/image/2.jpg'), title: '方式二', id: 2},
          style3: {url:require('../../assets/image/3.jpg'), title: '方式三', id: 3},
          style4: {url:require('../../assets/image/4.jpg'), title: '方式四', id: 4}
          ,
          typeoptions: [{
          value: 6,
          label: '6列'
        }, {
          value: 7,
          label: '7列'
        }, {
          value: 8,
          label: '8列'
        }, {
          value: 9,
          label: '9列'
        },
        ],
        timeoptions:[{
          value: 7,
          label: '一星期'
        }, {
          value: 14,
          label: '两星期'
        }, {
          value: 30,
          label: '一个月'
        }, {
          value: 90,
          label: '三个月'
        }],
        value: '',
        time: '',
        token: '',
            cateProps: {
            label: 'name', //看到的是哪个属性
            value: 'id', // 选中的是谁的值
            children: 'children' //哪个属性实现父子节点嵌套
        },
            options: [],
            stu_cat: [],
            schoolId: '',
            classId: '',
            studentList: [],
            editRecordDialogVisible: false,
            editRecordForm: {
            curvatureLeft: '',
            curvatureRight: '',
            cvaLeft: '',
            cvaRight: '',
            diopterLeft: '',
            diopterRight: '',
            eyeAxisLengthLeft: '',
            eyeAxisLengthRight: '',
            visionLeft: '',
            visionLeft: '',
            record_cat: '',
            studentName: '',
            schoolName:'',
            classesName:''
        },
        }
    },
    methods: {
         //获取级联选择器中的数据
        getOPtions() {
            let param = new URLSearchParams();
            param.append('token', this.token);
            axios({
                method: 'post',
                url: '/lightspace/cascade1',
                data: param
            }).then(this.handleGetOptionSucc.bind(this)).catch(this.handleGetOptionErr.bind(this))
        },
        handleGetOptionSucc (res) {
            if(res.data.status === 10204) {
                this.$message.error(res.data.msg);
                this.$router.push('/login');
            } else if(res.data.status == 200) {
                this.options =  res.data.data;
            }
        },
        handleGetOptionErr(err) {
            console.log(err)
        },
        //级联选择器选择变化会触发这个函数
        handleChange(value) {
           this.schoolId = this.stu_cat[0];
           this.classId = this.stu_cat[1];
        },
        seatQuery() {
            let param = new URLSearchParams();
            if(!this.token) {
                return this.$router.push('/login');
            }else if(!this.classId) {
                 return this.$message.error('请选择学校和班级');
            }else if(!this.value) {
                return this.$message.error('请选择排座方式');
            }else if(!this.time) {
                return this.$message.error('请选择排座周期');
            }else{
              let routeUrl = this.$router.resolve({
                path: '/detailSeatTwo',
                query: {
                  classId: this.classId,
                  type: this.value,
                  time: this.time
                }
               });
               window.open(routeUrl.href, '_blank');
              }
            },
        //排座类型
        handleTypeChange(value) {
          this.showTu = true;
            window.sessionStorage.setItem('type', value)
            this.studentList = [];
        },
        //排座周期
        changeTime(time) {
        }
    }

}
</script>
<style lang="less" scope>
.row {
  vertical-align: bottom;
  line-height: 30px;
  height: 30px;
  text-align: center;
}
.schoolSet {
    line-height: 40px;
    text-align: center;
}
.el-cascader {
    width: 100% !important;
}
.seat td   a {
  display: block;
  color: #000;
  text-decoration: none;
  font-size: 16px;
  text-align: center;
}
.seat img {
    margin: 5px 0;
}
.el-input.is-disabled .el-input__inner {
    background: #fff;
    text-align: center;
    font-weight: bold;
}
</style>



